<template lang="">
<!--모달창-->
  <div class="black-bg" v-if="this.modal == true">
    <div class="white-bg">
      <img :src="oneroom[clickstat].image" Style="width:100%">
      <h4>{{ oneroom[clickstat].title }}</h4>
      <p>{{ oneroom[clickstat].content }}</p>      
      <input v-model="month">
      <p>{{ month }}개월 선택함 : {{ oneroom[clickstat].price * month }} 원</p>
      <!--할인정보-->
      <DiscountBanner/>
      <button @click="$emit('CloseModal')">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name : 'Modal',
    data(){
      return{
        month : 1,
      }
    },
    watch : {
      month(a){
        if(a >= 13){
          alert("13이상 입력하지 마셈!");
          this.month = '1';
        }
        if(isNaN(a) == true){
          alert("숫자만 입력하셈!");
          this.month = '1';
        }        
      }
    },

    props : {
        oneroom : Array, 
        clickstat : Number,
        modal : Boolean,
    },

    updated(){
      if(this.month == 2){
        alert("222222222");
      }
    }    
}
</script>
