<template>

<Transition name="fade">
  <Modal @closeModal="modal = false" :oneroom="oneroom" :clickstat="clickstat" :modal="modal"/>
</Transition>  

<!--메인메뉴-->
<div class="menu">
  <a v-for="(a,i) in menu" :key="i">{{ a }}</a>
</div>

<!--할인정보-->
<DiscountBanner v-if="showDiscount == true" :sale=this.sale />

<button @click="priceSort()">가격순정렬(내림)</button>
<button @click="priceSort2()">가격순정렬(올림)</button>
<button @click="sortBack()">되돌리기</button>

<Contents @openModal="modal = true; clickstat = i" :oneroom="oneroom[i]" v-for="(a,i) in oneroom" :key="i"/>

</template>

<script>

import data from './assets/oneroom.js';
import DiscountBanner from './DiscountBanner.vue';
import Modal from './Modal.vue';
import Contents from './Contents.vue';

export default {
  name: 'App',
  data(){
    return {
      clickstat : 0,
      orgOneroom : [...data],
      oneroom : data,
      modal : false,
      menu : ['Home','Products','About'],
      products : ['역삼동원룸','천호동원룸','마포구원룸'],
      price : [80,90,60],
      신고수 : [0,0,0],
      showDiscount : true,
      sale : 50,
    }
  },
  methods : {
    increase(i){
      this.신고수[i]++;
    },
    priceSort(){
      this.oneroom.sort(function(a,b){
        return a.price - b.price
      })
    },
    priceSort2(){
      this.oneroom.sort(function(a,b){
        return b.price - a.price
      })
    },    
    sortBack(){
      this.oneroom = [...this.orgOneroom];
    },   
  },

  components: {
    DiscountBanner : DiscountBanner,  
    Modal : Modal,
    Contents : Contents,
  },

  mounted(){
    setInterval(() => {
      this.sale = this.sale -1;
    }, 1000);
  },



}
</script>

